.share {
  width: $mobile-width;
  height: 150px;
  position: relative;
  border-bottom: 1px solid $white-grey;

  &__header {
    text-transform: uppercase;
    width: 100%;
    margin-top: 10px;
    height: 24px;
    font-family: $font-family;
    font-size: 18px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: $gun-metal;
  }

  &__legend {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: absolute;
    top: 0px;

    &__container {
      width: $mobile-width;
    }
  }

  &__icon {
    color: $dull-orange;
  }

  &__button {
    width: 199px;
    height: 50px;
    padding: 13px 18px 13px 17px;
    border-radius: 12px;
    box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.32);
    background-color: $dull-orange;

    color: $whitetwo;
    font-size: 18px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-transform: uppercase;

    position: relative;
    top: 60px;
    left: 50%;
    transform: translateX(-50%);
  }
}
